import React, { Component } from 'react';
import Slider from 'react-slick';
import CardType1 from './CardType1.js';
import CardType2 from './CardType2.js';
import CardType3 from './CardType3.js';
import CardType4 from './CardType4.js';
import CardType5 from './CardType5.js';
import '../../styles/common.css';
import './style.css';

const _ = require('underscore');

class PriceList extends Component {
	constructor(props) {
        super(props);
        this.state = {
            visible: true,
            items: [],
            items1: []
        }
    }

    componentDidMount() {
    };

    renderSliderItem = (items) => {
        const { categories } = this.props;
        const elements = [];
        _.each(categories, (cat,i) => {
            if (cat.card_type === 1){
                elements.push(<div><CardType1 items={cat.data} /></div>)
            } else if (cat.card_type === 2){
                elements.push(<div><CardType2 items={cat.data} /></div>)
            } else if (cat.card_type === 3){
                elements.push(<div><CardType3 items={cat.data} /></div>)
            } else if (cat.card_type === 4){
                elements.push(<div><CardType4 items={cat.data} /></div>)
            } else if (cat.card_type === 5){
                elements.push(<div><CardType5 items={cat.data} /></div>)
            }
        });
        return elements;
    }

    render() {
        const { data, categories, onSlideChange } = this.props;
        const settings = {
          dots: false,
          infinite: true,
          speed: 500,
          autoplaySpeed: 10000,
          slidesToShow: 1,
          slidesToScroll: 1,
          autoplay: true,
          arrows: false,
          afterChange: onSlideChange
        };

        return (
            <div className='pricelist-wrapper'>
                <Slider {...settings}>
                    {this.renderSliderItem(categories)}
                </Slider>
            </div>
        );
    }
}

export default PriceList;




