import React, { Component } from 'react';
import { socketConnect } from 'socket.io-react';
import Categories from '../Categories';
import PriceList from '../PriceList';
import './style.css';

const _ = require('underscore');

const categories = !_.isEmpty(categories) ? categories : [
    {id: 1, name: 'BEER', icon: 'beer', has: ["BEERS"] , data:[], card_type: 1},
    {id: 2, name: 'BEER', icon: 'beer', has: ["FRESH BEER"] , data:[], card_type: 1},
    {id: 3, name: 'WHISKY', icon: 'whisky', has: ["WHISKY", "PREMIUM WHISKY"], data:[], card_type: 1},
    {id: 4, name: 'WHISKY', icon: 'whisky', has: ["AMERICAN IRISH WHISKY", "LIQUERS"], data:[], card_type: 3},
    {id: 5, name: 'WHISKY', icon: 'whisky', has: ["SINGLE MALTS", "SCOTCH WHISKY"], data:[], card_type: 4},
    {id: 6, name: 'VODKA', icon: 'vodka', has: ["VODKA"], data:[], card_type: 1},
    {id: 7, name: 'WINE AND CHAMPAGNE', icon: 'wine', has: ["WINES", "IMPORTED WINE", "CHAMPAGNE"], data:[], card_type: 4},
    {id: 8, name: 'RUM BRANDY GIN', icon: 'rum', has: ["RUM", "GIN"], data:[], card_type: 4},
    {id: 9, name: 'COCKTAILS', icon: 'cocktail', has: ["CLASSIC COCKTAILS"], data:[], card_type: 1},
    {id: 10, name: 'COCKTAILS', icon: 'cocktail', has: ["FRESH FRUIT COCKTAIL", "MARTINI"], data:[], card_type: 4},
    {id: 11, name: 'COCKTAILS', icon: 'cocktail', has: ["SPECIAL OFFER", "SUMMER SPECIAL MOJITO"], data:[], card_type: 4},
    {id: 12, name: 'SHOTS', icon: 'shots', has: ["SHOTS"], data:[], card_type: 1},
    {id: 13, name: 'NON ALCOHOLIC', icon: 'nonalcohol', has: ["NON ALCOHOLIC"], data:[], card_type: 1},
    {id: 14, name: 'NON ALCOHOLIC', icon: 'nonalcohol', has: ["MIXERS", "BREEZERS"], data:[], card_type: 4},
];

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            categories: categories,
            current_category: 'BEER'
        }
    }

    componentWillMount() {
        const { socket } = this.props
        socket.on('drinks', msg => this.makeCategories(msg));
        socket.on('pricechanged', msg => this.updateCategories(msg));
    }

    makeCategories(data) {
        this.setState({ data: data });
        _.each(this.state.categories, (cat) => {
            const thisData = _.filter(data.arr, (d) => {
                return cat.has.includes(d.category);
            });
            cat.data = thisData;
        });        
        this.setState({ categories: categories });
        return categories;
    }

    updateCategories(data) {
        const un = _;
        const categories = this.makeCategories(this.state.data);
        _.each(data.arr, (d) => {
            const thisData = _.find(categories, (cat)=>{
                return cat.has.includes(d.category);
            });
            const thisDrink = _.find(thisData.data, (td) => { return td.drinkId === d.drinkId });
            thisDrink.runningPrice = d.runningPrice;
        });
        this.setState({ categories: categories });
    }

    onSlideChange(e) {
        const categories = this.makeCategories(this.state.data);
        const thisCat = _.where(categories, { id: e+1 })[0];
        if(thisCat){
            this.setState({ current_category: thisCat.name });
        }
    }

    render() {
        return (
            <div className=''>
                <Categories categories={this.state.categories} currentCategory={this.state.current_category} />
                <div class='content-area'>
                	<PriceList categories={this.state.categories} onSlideChange={this.onSlideChange.bind(this)} />
                </div>
            </div>
        );
    }
}

export default socketConnect(Home);




